#gameDeals  > .tableContainer > table > tbody > tr > th, #gameDeals  > .tableContainer > table >tbody > tr > td{
    border: 1px solid white;
    text-align: center;
    width: 25%;
    background-color: #131213ab;
}

#gameDeals > h1, .gameCard > h1{
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

#gameDeals, .tableContainer{
    height: -webkit-fill-available;
    text-align: left;
}

#MenuItem {
    display: block;
    text-align: center;
}

#gameDeals > .tableContainer > table{
    border-collapse: collapse;
    width: 100%;
}

#gameDeals > .tableContainer > table >tbody > tr > td > button{
    margin: 0;
    padding: 0;
    width: 100%;
    border-radius: 0;
    border: 0;
    background-color: transparent;
    font-size: 100%;
    color: #6666ff;
    animation : fontColorRotate 5s linear 0s infinite;
}


@keyframes fontColorRotate {
    from {
        color: #6666ff;
    }
    10% {
        color: #0099ff;
    }
    50% {
        color: #00ff00;
    }
    75% {
        color: #ff3399;
    }
    100% {
        color: #6666ff;
    }
  }

    #gameDeals > .tableContainer > table {    
        display: inline-grid;
        overflow: hidden;
    }

    #gameDeals > .tableContainer > table thead tr {
      display: flex;
      width:100%;
    }
  
    #gameDeals > .tableContainer > table > tbody > tr{
        display: flex;
    }
    #gameDeals > h1{
        font-family: 'Kalam', cursive;
        font-size: 260%;
        margin-top: 0;
        margin-bottom: 0;
    }
    #gameDeals > .tableContainer>  table > tbody > tr > td > * {
        vertical-align: middle;
        text-align: center;
        display: inline;
    }

    .platformSelector, .currencySelector{
        background-color: white;
        margin: 5px;
    }
    
    #gameDeals > div > div >.MuiSelect-select{
        width: 5em;
        padding: 0.25em 1.2em;
        border: 0px #ffffff00 solid !important;
    }
    #gameDeals > .MuiOutlinedInput-root{
        margin-bottom: 5px;
        border: 0px #ffffff00 solid !important;
    }
    .headerTable{
        width: calc( 100% - 10px ) !important;
    }
    .bodyTable{
        max-height: 80%;
        table-layout: fixed;
        height: auto;
        overflow-y: scroll !important;
    }

    #gameDeals > .selectorsDiv > div > fieldset{
        border: transparent;
        border-width: 0;
    }