.customGameComponent{
    height: inherit !important;
    margin: auto;
}

.customGameComponent > div > .select {
    width: 20em;
    display: block;
    margin: 0 auto 1em auto;
    background-color: rgba(240, 248, 255, 0.7);
}

.customGameComponent > #gameDeals > .loaderDiv > span{
    left: calc( 50% - 150px);
}

.gameFound{
    animation : fontColorRotate 5s linear 0s infinite;
}

.customGameComponent > #gameDeals{
    width: 90%;
    margin: auto;
}

.customGameComponent > #gameDeals > h1{
    animation : fontColorRotate 5s linear 0s infinite;
}

@keyframes fontColorRotate {
    from {
        color: #6666ff;
    }
    10% {
        color: #0099ff;
    }
    50% {
        color: #00ff00;
    }
    75% {
        color: #ff3399;
    }
    100% {
        color: #6666ff;
    }
}


@media screen and (max-width: 1500px) {
    .customGameComponent > div > button{
        width: 30%;
    }
}

@media screen and (max-width: 1200px) {
    .customGameComponent > div > button{
        width: 40%;
    }
}

@media screen and (max-width: 900px) {
    .customGameComponent > div > button{
        width: 50%;
    }
}

@media screen and (max-width: 600px) {
    .customGameComponent > div > button{
        width: 60%;
    }
    .customGameComponent {
        overflow-x: hidden;
    }

    .customGameComponent > #gameDeals {
        width: calc(100% + 10px);
        margin: auto;
    }
}

@media screen and (max-width: 500px) {
    .customGameComponent > div > button{
        width: 80%;
    }
}