@import url('https://fonts.googleapis.com/css2?family=Kalam&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Handlee&display=swap');

.name{
    font-family: 'Handlee', cursive;
    font-size: 2em;
    margin-top: 0;
    margin-bottom: 0;
}

.scoreRow{
    margin: 0 3%;
    font-size: 120%;
    display: inline-flex;
    font-family: 'Handlee', cursive;
}

.generalInfoDiv {
    height: fit-content;
}
.generalInfoDiv > h3{
    font-family: 'Handlee', cursive;
    font-size: 120%;
    margin-top: 0;
    margin-bottom: 0;
}

.generalInfoDiv > img{
    max-width: 20%;
    display: inline-block;
    margin: 0 1%;
}

.gameCard > p{
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow-y: scroll;
    overflow-x: hidden;
    margin: 0 auto 0.2em auto;
    height: 5em; 
}

.gameCard > button{
    width: 90%;
    font-size: 130%;
    margin-bottom: 0.6rem !important;
    font-family: 'Kalam', cursive;
}

#gameCardGeneralInfo{
    display: inline-flex;
    height: 4em;
}

#gameCardGeneralInfo > div{
    width: 33%;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.gameCard > .gamePic{
    width:100%;
    height: 12rem;
    object-fit: fill;
}

.gameCard > div > button{
    width: 90%;
    font-size: 130%;
    margin-bottom: 0.6rem !important;
    font-family: 'Kalam', cursive;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@media screen and (max-width: 400px) {
    .name{
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}